.area{
    position: absolute;
    color: white;
    left: 50%;
    transform: translateX(-50%);
    top: 10px;
} 


.mainbackground{
    width: 700px;
    height: 500px;
    border: 1px gray solid;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
}



.MainCharacter{
    position: absolute;
    height: 100%;
    width: 50%;
    left: 0;
    top: 0;
    border: 1px green solid;
    text-align: left;
}

.Enemy{
    position: absolute;
    height: 100%;
    width: 50%;
    right: 0;
    top:0;
    border: 1px orange solid;
    text-align: right;
}




@keyframes Attack {
    0%{left: 0;}
    60%{left: 100px;}
    100%{left: 0;}
}

@keyframes EAttack {
    0%{right: 0;}
    60%{right: 100px;}
    100%{right: 0;}
}

.E{
    position: absolute;
    right: 10px;
}

.stats{
    display: inline;
    position: absolute;
    top:300px;
}

.Charactername{
    position: absolute;
    top: 210px; ;
}

.hp{
    color:red;
    display: inline-block;
}





.Butt{
    position: absolute;
    background-color: white;
    z-index: 100;
    top: 390px;
}